import React,{ Component } from "react";
import './navStyle.css' ;
import {
    Navbar,
    Nav,
    NavDropdown
} from 'react-bootstrap';
import { Link, useHistory } from "react-router-dom";

const NavBar = (props) => {
        let history = useHistory();
        const {
            location : { pathname }
        } = history;

        const pushHistory = pathname => {
            history.push({ pathname });
        };  

        return (
            <Navbar collapseOnSelect expand="lg" bg="#fff" variant="light">
                <Navbar.Brand  to="/"><img width={props.width} height={props.height} src={props.logo} alt='CODEZILA'/></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto navbar-custom">
                        <Nav.Link 
                            className={` ${
                                    pathname.includes("/home") ? "active" :  ( pathname.includes("/#")  ? "active" : "" )
                                }`} 
                            onClick={() => "home" && pushHistory("home")}
                            to="/home"
                            >Home</Nav.Link>
                        <Nav.Link 
                            className={` ${
                                    pathname.includes("/learn") ? "active" : " "
                                }`} 
                            onClick={() => "learn" && pushHistory("learn")}
                            to="/learn"
                            >Learning</Nav.Link>
                        {/* <NavDropdown disabled title="Quiz" id="collasible-nav-dropdown" class="ddown-custom">
                            <NavDropdown.Item href="#dq">Daily Quiz</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Saturday Task</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.3">Sunday Contest</NavDropdown.Item>
                        </NavDropdown> */}
                        <Nav.Link href="#/home#about" >About Us</Nav.Link>
                        <Nav.Link to="#contact">Contact Us</Nav.Link>
                    </Nav>
                    {/* <Nav class="navbar-custom">
                    <Nav.Link disabled eventKey={2} href="#">
                        Login
                    </Nav.Link>
                    </Nav> */}
                </Navbar.Collapse>
            </Navbar>      
        );
}

export default NavBar;