import React, { Component } from 'react';
import './App.css';
import logo from "./res/logo_tagline.svg";
import {
  Route,
  Switch,
  withRouter,
  HashRouter as Router
} from "react-router-dom";  

import Adi from './Adi';
import Home from "./Home";
import NavBar from './components/nav/nav-bar';
import Footer from './components/footer/footer';
import Ace from './components/quiz/editor';
import { ParallaxProvider } from 'react-scroll-parallax';
import Learn from './components/learn/learn';

const Join = () =>{
  return(
      <iframe style={{border : "none", width: '100%', height: '100vh', backgroundColor: 'rgba(0, 0, 0, 0) !important' }} 
      src={'https://docs.google.com/forms/d/e/1FAIpQLSetOPK26qZ0PDEHDVp4CO9T_P0xoGAsamA5IpomAZfySLVCZQ/viewform'} />    
   )
}

const Head = () => {
  return(
      <div class='Header' id="header" sticky='top'>
        <NavBar  class='ontop' logo={logo} width='50%'/>
      </div>
  );
};

const Header = withRouter(Head);

function App() {  

  return (
    <Container-Fluid>
                <div id="App">
                    <Header/>
                </div>            
                <div id="dq" class='App' >
                        <Switch>
                          <Route path="/" component={Home} exact/>
                          <Route path="/home" component={Home} exact/>
                          <Route path="/admin" component={Adi} exact/>
                          <Route path="/learn"  component={Learn} exact/>
                          <Route path="/ed" component={Ace} exact/>
                          <Route path="/join" component={Join} exact />
                          <Route component={Error}/>
                        </Switch>
                </div>
                <div class='footer'>      
                  <Footer/>
                </div>     
      </Container-Fluid>
  );
}

var prevScrollpos = window.pageYOffset;
        window.onscroll = function() {
          var currentScrollPos = window.pageYOffset;
          if (prevScrollpos === currentScrollPos) {
            document.getElementById("header").style.boxShadow = "none";
          } else {
            document.getElementById("header").style.boxShadow = "0px 2px 4px rgba(0, 0, 0, 0.1)";
          }
}
class AppContainer extends Component {
    render() {
        const {
      history: {
        location: { pathname }
      },
      history
    } = this.props;
    if (!pathname || pathname === "/") {
      history.push({ pathname: "/home" });
    }
        return (
            <ParallaxProvider>
                <App />
            </ParallaxProvider>
        );
    }
}

export default withRouter(AppContainer);
